<template>
  <!-- tag -->
  <el-tag
    v-if="columnProp.cellType && columnProp.cellType == 'tag'"
    :type="columnProp.tagOptions[rowData[columnProp.field]]"
  >
    {{ columnProp.formatter(rowData) }}
  </el-tag>
  <!-- avatar -->
  <el-avatar
    v-if="columnProp.cellType && columnProp.cellType == 'avatar'"
    shape="square"
    :size="60"
    fit="fill"
    :src="rowData[columnProp.field]"
  />
  <!-- image -->
  <el-image
    v-if="columnProp.cellType && columnProp.cellType == 'image'"
    fit="fill"
    :src="rowData[columnProp.field]"
    style="height: 60px"
  />
  <!-- rate -->
  <el-rate
    v-if="columnProp.cellType == 'rate'"
    v-model="rowData[columnProp.field]"
    :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
    :max="columnProp.maxRate > 0 ? columnProp.maxRate : 5"
    style="margin-top: 8px"
    :disabled="true"
  />
  <!-- 文本域 -->
  <el-input
    v-if="columnProp.cellType == 'textarea'"
    v-model="rowData[columnProp.field]"
    placeholder=""
    :autosize="
      columnProp.cellTextareaSize ? columnProp.cellTextareaSize : { minRows: 2, maxRows: 4 }
    "
    type="textarea"
    class="textarea_readonly"
    :readonly="true"
  />
</template>

<script>
export default {
  name: "TableCell",
  props: ["columnProp", "rowProp"],
  computed: {
    rowData: {
      get() {
        return this.rowProp
      },
      set(value) {
        this.$emit("update:rowProp", value)
      },
    },
  },
}
</script>
