<template>
  <div class="app-container">
    <!-- ////////// 工具栏 ////////// -->
    <div class="filter-container">
      <!-- 排序 -->
      <span style="font-size: 14px; font-weight: bold; vertical-align: 25%; color: gray"
        >排&nbsp;&nbsp;&nbsp;&nbsp;序：</span
      >
      <!-- 排序字段 -->
      <el-select
        v-model="listPagination.sort"
        placeholder="SortFiled"
        style="width: 120px; margin-right: 10px"
        class="filter-item"
        @change="handleFilter"
      >
        <el-option
          v-for="item in sortColumns"
          :key="item.field"
          :label="item.label"
          :value="item.field"
        />
      </el-select>
      <!-- 排序方式 -->
      <el-select
        v-model="listPagination.sortType"
        placeholder="SortType"
        style="width: 100px; margin-right: 10px"
        class="filter-item"
        @change="handleFilter"
      >
        <el-option
          v-for="item in sortTypeOptions"
          :key="item"
          :label="item == 'asc' ? '顺序' : '逆序'"
          :value="item"
        />
      </el-select>
      <!-- 选项设置 -->
      <!-- <el-card
        shadow="hover"
        style="display: inline-block; height: 36px; vertical-align: -50%; margin-left: 20px"
        body-style="padding:2px"
      > -->
      <el-checkbox
        v-model="showEditableOnly"
        class="filter-item"
        style="margin-right: 18px"
        @change="tableKey = tableKey + 1"
      >
        仅显示可修改列
      </el-checkbox>
      <el-checkbox
        v-model="showIdxColumn"
        class="filter-item"
        style="margin-right: 18px"
        @change="tableKey = tableKey + 1"
      >
        显示序号
      </el-checkbox>
      <el-checkbox v-model="fixActionColumn" class="filter-item" @change="tableKey = tableKey + 1">
        固定操作列
      </el-checkbox>
      <el-checkbox
        v-if="expandColumns.length"
        v-model="expandAll"
        class="filter-item"
        @change="tableKey = tableKey + 1"
      >
        展开所有行
      </el-checkbox>
      <!-- </el-card> -->
      <!-- 换行 -->
      <br />
      <!-- 字段搜索 -->
      <span style="font-size: 14px; font-weight: bold; vertical-align: 25%; color: gray"
        >关键字：</span
      >
      <template v-for="(item, index) in listQueryOptions" :key="index">
        <!-- 文本输入 -->
        <el-input
          v-if="!item.type || item.type == 'input'"
          v-model="listQueryParam[item.field]"
          :placeholder="item.label"
          clearable
          :style="'width: ' + item.width + '; margin-right: 10px'"
          class="filter-item"
          @keyup.enter="handleFilter"
        />
        <!-- 下拉选择 -->
        <el-select
          v-if="item.type == 'select'"
          v-model="listQueryParam[item.field]"
          :placeholder="item.label"
          clearable
          :style="'width: ' + item.width + '; margin-right: 10px'"
          class="filter-item"
          @change="handleFilter"
        >
          <el-option
            v-for="v in item.selectOptions"
            :key="v.value"
            :label="v.label"
            :value="v.value"
          />
        </el-select>
      </template>
      <!-- 操作按钮 -->
      <el-button v-waves class="filter-item" type="primary" icon="Search" @click="handleFilter">
        查询
      </el-button>
      <el-button
        class="filter-item"
        style="margin-left: 10px"
        type="primary"
        icon="Edit"
        @click="handleCreate"
      >
        新增
      </el-button>
      <el-button
        v-waves
        :loading="downloadLoading"
        class="filter-item"
        type="primary"
        icon="Download"
        @click="handleExport"
      >
        导出
      </el-button>
    </div>

    <!-- ////////// 表格 ////////// -->
    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list"
      border
      :default-expand-all="expandAll"
      fit
      stripe
      highlight-current-row
      :max-height="950"
      style="width: 100%"
      @sort-change="sortChange"
    >
      <!-- 展开标识 -->
      <el-table-column v-if="expandColumns.length" width="36px" type="expand" fixed="left">
        <template #default="props">
          <el-icon size="20px" color="lightgrey" style="position: absolute; top: 45px; left: 36px">
            <Expand />
          </el-icon>
          <div style="display: inline-block; margin-left: 86px; margin-bottom: 10px">
            <!-- <span v-for="(item, index) in expandColumns" :key="index"
              >{{ item.label }}: {{ props.row[item.field] }}</span
            > -->
            <el-table :data="[props.row]" :border="false">
              <el-table-column
                v-for="(item, index) in expandColumns"
                :key="index"
                :prop="item.field"
                :label="item.label"
                :width="item.width"
                :formatter="item.formatter"
                align="left"
              >
                <template v-slot="{ row }">
                  <TableCell v-if="item.cellType" :columnProp="item" :rowProp="row" />
                </template>
              </el-table-column>
            </el-table>
          </div>
        </template>
      </el-table-column>
      <!-- 表格序号列 -->
      <el-table-column v-if="showIdxColumn" label="序号" width="50px" align="center" fixed="left">
        <template v-slot="props">
          <!-- {{ scope.$index + (listPagination.pageIndex - 1) * listPagination.pageSize + 1 }} -->
          {{ props.$index + 1 }}
        </template>
      </el-table-column>
      <!-- 表格动态列 -->
      <el-table-column
        v-for="(item, index) in initialColumns"
        :key="index"
        :prop="item.field"
        :label="item.label"
        :width="item.width"
        :min-width="item.minWidth"
        :show-overflow-tooltip="item.showOverflowTooltip"
        :formatter="item.formatter"
        align="left"
      >
        <template v-slot="{ row }">
          <TableCell v-if="item.cellType" :columnProp="item" :rowProp="row" />
        </template>
      </el-table-column>
      <!-- 表格操作列 -->
      <el-table-column
        label="操作"
        align="center"
        width="140"
        :fixed="fixActionColumn ? 'right' : null"
        class-name="small-padding fixed-width"
      >
        <template v-slot="{ row, $index }">
          <el-button type="primary" size="small" @click="handleUpdate(row)"> 修改 </el-button>
          <el-button type="danger" size="small" @click="handleDelete(row, $index)">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- ////////// 底部分页栏 ////////// -->
    <pagination
      v-show="total > 0"
      :total="total"
      v-model:page="listPagination.pageIndex"
      v-model:limit="listPagination.pageSize"
      @pagination="getList"
    />

    <!-- ////////// 修改对话框 ////////// -->
    <el-dialog v-model="dialogEditVisible" :title="dialogTitle[dialogStatus]" width="480px">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
        label-width="70px"
        style="width: 400px; margin-left: 10px"
      >
        <!-- 动态行 -->
        <el-form-item
          v-for="(item, index) in editableColumns"
          :key="index"
          :label="item.label"
          :prop="item.field"
          :required="item.required"
        >
          <!-- 文本输入框 -->
          <el-input
            v-if="!item.editType || item.editType == 'input'"
            v-model="temp[item.field]"
            :placeholder="'请输入' + item.label"
          />
          <!-- 选择器 -->
          <el-select
            v-if="item.editType == 'select'"
            v-model="temp[item.field]"
            :placeholder="'请选择' + item.label"
            class="filter-item"
            :value-key="item.value"
          >
            <el-option
              v-for="v in item.selectOptions"
              :key="v.value"
              :label="v.label"
              :value="v.value"
            />
          </el-select>
          <!-- 开关 -->
          <el-switch
            v-if="item.editType == 'switch'"
            v-model="temp[item.field]"
            class="ml-2"
            style="--el-switch-on-color: #13ce66; --el-switch-off-color: #ff4949"
            :active-value="item.selectOptions[0].value"
            :active-text="item.selectOptions[0].label"
            :inactive-value="item.selectOptions[1].value"
            :inactive-text="item.selectOptions[1].label"
          />
          <!-- 日期选择器 -->
          <!-- 可用类型：year/month/date/dates/datetime/week/datetimerange/daterange/monthrange -->
          <el-date-picker
            v-if="
              [
                'year',
                'month',
                'date',
                'dates',
                'datetime',
                'week',
                'datetimerange',
                'daterange',
                'monthrange',
              ].indexOf(item.editType) > -1
            "
            v-model="temp[item.field]"
            :placeholder="'请选择时间：' + item.label"
            :type="item.editType"
          />
          <!-- 评分 -->
          <el-rate
            v-if="item.editType == 'rate'"
            v-model="temp[item.field]"
            :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
            :max="item.maxRate > 0 ? item.maxRate : 5"
            style="margin-top: 8px"
          />
          <!-- 文本域 -->
          <el-input
            v-if="item.editType == 'textarea'"
            v-model="temp[item.field]"
            :placeholder="'请输入' + item.label"
            :autosize="item.formTextareaSize ? item.formTextareaSize : { minRows: 2, maxRows: 4 }"
            type="textarea"
          />
          <!-- ////// 额外组件 ////// -->
          <!-- 图片展示 -->
          <el-image
            v-if="item.additionalFormItem && item.additionalFormItem.type == 'image'"
            fit="fill"
            :src="temp.portrait"
            :style="
              item.additionalFormItem.style
                ? item.additionalFormItem.style
                : 'margin-top: 12px; height: 100px'
            "
          />
          <!-- 按钮 -->
          <el-button
            v-if="item.additionalFormItem && item.additionalFormItem.type == 'button'"
            type="primary"
            size="small"
            :style="
              item.additionalFormItem.style ? item.additionalFormItem.style : 'margin-top: 12px'
            "
            @click="item.additionalFormItem.callback(temp[item.field])"
          >
            {{ item.additionalFormItem.label }}
          </el-button>
        </el-form-item>
      </el-form>
      <!-- 弹窗底部 -->
      <template v-slot:footer>
        <div class="dialog-footer">
          <el-button @click="dialogEditVisible = false"> 取消 </el-button>
          <el-button
            type="primary"
            @click="dialogStatus === 'create' ? createData() : updateData()"
          >
            确认
          </el-button>
        </div>
      </template>
    </el-dialog>

    <!-- ////////// 删除对话框 ////////// -->
    <el-dialog v-model="dialogDeleteVisible" title="删除" width="400px" draggable>
      <span>确认删除本条记录吗？</span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogDeleteVisible = false">取消</el-button>
          <el-button type="danger" @click="deleteData()"> 确认删除 </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import waves from "@/directive/waves" // waves directive 已失效，待修复
import { parseTime } from "@/utils/index"
import Pagination from "@/components/Pagination" // secondary package based on el-pagination
import TableCell from "./TableCell.vue"

export default {
  name: "CommonDataTable",
  props: ["tableColumns", "getListData", "createRow", "updateRow", "deleteRow"],
  components: { Pagination, TableCell },
  directives: { waves },
  data() {
    return {
      // 搜索栏配置
      listQueryOptions: {},
      // 搜索条件
      listQueryParam: {},
      // 分页条件
      listPagination: {
        sort: "id", // 默认按id排序（首字母不要大写）
        sortType: "desc",
        pageIndex: 1,
        pageSize: 10,
      },
      tableKey: 0, //key必须变化表格属性设置修改才能生效
      list: null, //表格数据
      total: 0, //总行数
      listLoading: true,
      downloadLoading: false,
      sortTypeOptions: ["asc", "desc"],
      sortColumns: [], //排序字段
      statusOptions: ["published", "draft", "deleted"],
      showEditableOnly: false, //是否仅显示可修改的列
      showIdxColumn: false, //是否显示序号
      fixActionColumn: false, //是否固定操作列
      expandAll: false, //是否展开所有行
      temp: {},
      dialogEditVisible: false,
      dialogDeleteVisible: false,
      dialogStatus: "",
      dialogTitle: {
        update: "修改",
        create: "新增",
      },
      rules: {}, //校验规则，已根据配置信息自动生成
    }
  },
  created() {
    // 根据表格列信息初始化排序字段
    this.sortColumns = this.tableColumns.map((item) => {
      return {
        field: item.field,
        label: item.label,
      }
    })
    // 根据表格列信息初始化搜索字段配置
    this.listQueryOptions = this.tableColumns
      .filter((item) => {
        return item.queryable
      })
      .map((item) => {
        this.listQueryParam[item.field] = undefined
        return {
          field: item.field,
          label: item.label,
          width:
            item.queryBoxWidth.length > 0
              ? item.queryBoxWidth
              : item.width.length > 0
              ? item.width
              : item.minWidth,
          type: item.queryBoxType,
          selectOptions: item.selectOptions,
        }
      })
    // 根据表格列信息设置必填项校验规则
    for (let item of this.tableColumns) {
      if ((item.required || item.validator) && this.rules[item.field] == undefined) {
        this.rules[item.field] = [] //验证规则子项初始化
      }
      if (item.required) {
        //添加必填项验证规则
        this.rules[item.field] = this.rules[item.field].concat([
          { required: true, message: item.label + "不能为空", trigger: "blur" },
          { required: true, message: item.label + "不能为空", trigger: "change" },
        ])
      }
      if (item.validator) {
        //添加自定义验证规则
        this.rules[item.field] = this.rules[item.field].concat([
          { validator: item.validator, trigger: "blur" },
        ])
      }
    }
    // 获取列表
    this.getList()
  },
  methods: {
    getList() {
      this.listLoading = true
      this.getListData(this.listQueryParam, this.listPagination).then((response) => {
        this.list = response.data.data
        this.total = response.data.total
        this.listLoading = false
        // 下面通过延时模拟请求时转圈的效果
        // setTimeout(() => {
        //   this.listLoading = false
        // }, 0.5 * 1000)
      })
    },
    handleFilter() {
      this.listPagination.pageIndex = 1
      this.getList()
    },
    // handleModifyStatus(row, status) {
    //   this.$message({
    //     message: "操作成功",
    //     type: "success",
    //   })
    //   row.userStatus = status
    // },
    sortChange(data) {
      const { prop, order } = data
      this.listPagination.sort = prop
      this.listPagination.sortType = order === "ascending" ? " asc " : " desc "
      this.handleFilter()
    },
    resetTemp() {
      this.temp = {}
    },
    //处理数据，初始化字段，与后端对应（否则可能无法接收）
    handleTempData() {
      // console.log("nonNullColumns ", this.nonNullColumns) //调试
      // for (let item in this.nonNullColumns) {
      //   if (this.temp[item] == undefined || this.temp[item] == null) {
      //     this.temp[item] = ""
      //   }
      // }
      for (let item in this.list[0]) {
        if (this.temp[item] == undefined || this.temp[item] == null) {
          this.temp[item] = ""
        }
      }
    },
    handleCreate() {
      this.resetTemp()
      this.dialogStatus = "create"
      this.dialogEditVisible = true
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate()
      })
    },
    createData() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          this.handleTempData()
          this.createRow(this.temp).then(() => {
            // this.list.unshift(this.temp) //这种方式不能显示系统自动创建的数据
            this.handleFilter() //重新获取列表数据
            this.dialogEditVisible = false
            this.$notify({
              title: "Success",
              message: "新增成功",
              type: "success",
              duration: 2000,
            })
          })
        }
      })
    },
    handleUpdate(row) {
      this.temp = Object.assign({}, row) // copy obj
      // this.temp.timestamp = new Date(this.temp.timestamp)
      this.dialogStatus = "update"
      this.dialogEditVisible = true
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate()
      })
    },
    updateData() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          const tempData = Object.assign({}, this.temp)
          // tempData.token = ""
          // tempData.timestamp = +new Date(tempData.timestamp) // change Thu Nov 30 2017 16:41:05 GMT+0800 (CST) to 1512031311464
          this.updateRow(tempData).then(() => {
            // const index = this.list.findIndex((v) => v.id === this.temp.id) // 根据id找到更新的位置
            // this.list.splice(index, 1, this.temp) // 更新列表对应位置的数据
            this.handleFilter() //重新获取列表数据
            this.dialogEditVisible = false // 关闭对话框
            this.$notify({
              title: "Success",
              message: "修改成功",
              type: "success",
              duration: 2000,
            })
          })
        }
      })
    },
    handleDelete(row, index) {
      this.resetTemp()
      this.temp.ids = row.id
      this.temp.index = index
      this.dialogDeleteVisible = true
    },
    deleteData() {
      this.deleteRow(this.temp.ids).then(() => {
        this.list.splice(this.temp.index, 1) // 删除列表对应行
        this.$notify({
          title: "Success",
          message: "删除成功",
          type: "success",
          duration: 2000,
        })
      })
      this.dialogDeleteVisible = false // 关闭对话框
    },
    // 导出数据到Excel表格
    handleExport() {
      this.downloadLoading = true
      import("@/vendor/Export2Excel").then((excel) => {
        const tHeader = this.tableColumns.map((item) => item.field)
        const data = this.list.map((v) =>
          tHeader.map((j) => {
            if (v[j] instanceof Date) return parseTime(v[j])
            else return v[j]
          })
        )
        excel.export_json_to_excel({ header: tHeader, data, filename: "table-list" })
        this.downloadLoading = false
      })
    },
  },
  computed: {
    // 过滤出可修改的列
    editableColumns: function () {
      return this.tableColumns.filter((column) => {
        return column.editable
      })
    },
    // 过滤出要显示的列
    showColumns: function () {
      return this.showEditableOnly ? this.editableColumns : this.tableColumns
    },
    // 过滤出最初展示的列
    initialColumns: function () {
      return this.showColumns.filter((column) => {
        return !column.expand
      })
    },
    // 过滤出要折叠的列
    expandColumns: function () {
      return this.showColumns.filter((column) => {
        return column.expand
      })
    },
    // 过滤出不可为空的字段
    nonNullColumns: function () {
      return this.tableColumns
        .filter((column) => {
          return column.nonNull
        })
        .map((item) => {
          return item.field
        })
    },
  },
}
</script>

<style lang="scss">
.el-table__row {
  .textarea_readonly {
    .el-textarea__inner {
      // background: transparented !important;
      --el-input-bg-color: transparent;
      --el-input-border: 0 solid transparent;
      --el-input-border-color: transparent;
      --el-input-hover-border-color: transparent;
      --el-input-focus-border-color: transparent;
    }
  }
}

